import { BiChevronRightCircle } from 'react-icons/bi'
import styles from './Todo.module.css'

function Todo(props) {
  const { todoText, index, delTodo } = props
  return (
    <div className={styles.todoBlock} onDoubleClick={(e) => delTodo(index)}>
      <BiChevronRightCircle className={styles.todoTask} />
      <div className={styles.todoText}>{todoText}</div>
    </div>
  )
}

export default Todo
